* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    background-color: #c5cae9;
}

#root {
    width: 100%;
    height: 100%;
}

#main-container {
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    width: 50%;
    height: 60%;
    border-radius: 40px;
}